import Icon from '../icon';
import Item from './item';

export default class Others extends Item {

  constructor(clicked) {
    super('div', '');
    this.clicked = clicked;
  }

  element() {
    let span = document.createElement('span');
    span.innerHTML = '';
    return super.element()
      .child(span)
      .on('click', () => {
        if (this.clicked) {
          this.clicked(this);
        }
      });
  }
}
